import React, { Component } from 'react';
import logo from '../../content/assets/images/ceremi-logoWeb.png'
import NavLink from '../components/Nav'
import styled from "styled-components"


const Container = styled.div`
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    background: transparent;
    z-index: 999;
    display: flex;
    margin: auto;
    width: 85%;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    height: 120px;
` 
const Col = styled.div`
    padding: 25px 30px;
    flex: 1 auto;
   
`
class Header extends Component {
    render() {
        return (
                <Container>
                    <Col>
                    <div 
                        style={{
                            display: `flex`,
                            flexDirection:  `row`,
                            alignItems: `center`,
                            justifyContent: `center`,
                            textTransform: `uppercase`,
                        }}
                    >
                        <img 
                            style={{
                                margin: `0`,
                            }}
                            src={logo} 
                            alt='Ceremi Logo' 
                            Width="40"
                        />
                        <span
                            style={{
                                fontSize: `1.3rem`,
                                fontWeight: `600`,
                                lineHeight: `1.4rem`,
                                paddingLeft: `20px`,
                                margin: `0`,
                            }}
                        >
                            Ministerio CEREMI
                        </span>
                    </div>
                    </Col>
                    <Col>
                        <NavLink
                         to="/"
                        >
                            Inicio
                        </NavLink>
                        <NavLink to ="/blog">Notas Pastorales</NavLink>
                        <a
                            style={{
                                textDecoration: `none`,
                                display: `inline-block`,
                                color: `#21539e`,
                                margin: `0 1rem`,
                                padding: `0.25rem`,
                                boxShadow: `none`,
                                textTransform: `uppercase`
                            }}
                            href="https://www.facebook.com/CeremiInternacional/"
                            target="_blank"
                        >
                            Facebook
                        </a>
                    </Col>
                    <Col>Donate button</Col>
                </Container>
        );
    }
}

export default Header;